* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-title{
  font-size: 40px;
  font-weight: 700;
  margin-left: 10px;
}


p{
  font-size: 16px;
  font-weight: 700;
}

.title__label{
  font-weight: 400;
}

.wrapper{
  display: flex;
  align-items: center;
}

.country-flag{
  display: block;
}

.country-name{
  display: block;
  margin-left: 10px;
  font-weight: 500;
}